*{
  margin: 0;
  padding: 0;
}

body{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.App{
  height: 100vh;
  background-color: #dadbd3;
  display: grid;
  place-items: center;
}

.app__body{
   height: 90vh;
   width: 90vw;
   background-color: #ededed;
   box-shadow: -1px 4px 20px -6px #000;
   display: flex;
}